                          </v-tooltip>
                          <v-tooltip bottom>
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn icon @click="del2" v-bind="attrs" v-on="on" style="margin-top: 10px;">
                                <v-icon>mdi-delete</v-icon>
                              </v-btn>
                            </template>
