                          </v-tooltip>
                          <v-tooltip bottom>
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn icon @click="class_choosed = ''; dialog2 = true; file_choosed = pk2" v-bind="attrs" v-on="on"
                                style="margin-top: 10px;">
                                <v-icon>mdi-application</v-icon>
                              </v-btn>
                            </template>
                            <span>预览</span>
                          </v-tooltip>
                          <v-tooltip bottom>
                            <template v-slot:activator="{ on, attrs }">
                              <v-btn icon @click="class_choosed = ''; sheet3 = !sheet3; file_choosed = pk2" v-bind="attrs" v-on="on"
                                style="margin-top: 10px;">
                                <v-icon>mdi-share-variant</v-icon>
                              </v-btn>
                            </template>
                            <span>推送</span>
                          </v-tooltip>
                        </v-row>
                      </v-list-item-action>
                    </v-list-item>
                    <v-divider v-if="index != papers.length" :key="index"></v-divider>
                  </template>
                </v-list-item-group>
              </v-list>
            </v-card>
          </v-col>
