      <v-btn @click="sheet2 = !sheet2" color="secondary" depressed fab large dark class="v-btn--example"
        style="position: fixed; top: 550px; right: 15px;">
        上传
      </v-btn>

      <!-- 推送sheet1 -->
      <v-bottom-sheet v-model="sheet1" inset>
        <v-sheet class="text-center" height="450px">
          <v-card flat color="transparent" class="mx-auto">
            <div style="margin: 10px 10px;">
              <v-card-title class="`text-$subtitle-1`" style="font-weight:bold;">推送</v-card-title>
            </div>
            <v-card-text>
              <v-row>
                <v-col class="pr-4">
                  <v-subheader>选择班级</v-subheader>
                  <v-list flat>
                    <v-list-item-group v-model="class_choosed" active-class="orange--text">
                      <v-list-item v-for="(item, index) in classes" :key="index">
                        <v-list-item-content>
                          <v-list-item-title>{{ item }}</v-list-item-title>
                        </v-list-item-content>
                      </v-list-item>
                    </v-list-item-group>
                  </v-list>

                  <v-subheader>设定时间</v-subheader>
                  <v-slider v-model="time" prepend-icon="mdi-alarm" class="align-center" :max="max" :min="min" hide-details>
                    <template v-slot:append>
                      <v-text-field v-model="time" class="mt-0 pt-0" hide-details single-line type="number"
                        style="width: 50px"></v-text-field>
                    </template>
                  </v-slider>

                  <v-tooltip bottom>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn color="warning" fab :loading="loading" @click="push_paper" v-bind="attrs" v-on="on"
                        style="margin-top: 10px; margin-left: 30px;">
                        <v-icon>mdi-arrow-right-bold-outline</v-icon>
                      </v-btn>
                    </template>
                    <span>提交</span>
                  </v-tooltip>

                </v-col>
              </v-row>
            </v-card-text>
          </v-card>
        </v-sheet>
      </v-bottom-sheet>

      <!-- 推送sheet3 -->
      <v-bottom-sheet v-model="sheet3" inset>
        <v-sheet class="text-center" height="450px">
          <v-card flat color="transparent" class="mx-auto">
