      </v-bottom-sheet>

      <!-- 上传sheet2 -->
      <v-bottom-sheet v-model="sheet2" inset>
        <v-sheet class="text-center" height="450px">
          <v-card flat color="transparent" class="mx-auto">
            <div style="margin: 10px 10px; margin-bottom: 30px;">
              <v-card-title class="`text-$subtitle-1`" style="font-weight:bold;">上传文件</v-card-title>
            </div>
            <v-file-input id="file" :rules="rules" placeholder="上传文件" chips label="文件" outlined show-size dense
              prepend-icon="mdi-paperclip" style="margin-top: 30px;" width="50px"></v-file-input>
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-btn color="warning" fab :loading="loading2" @click="upload" v-bind="attrs" v-on="on"
                  style="margin-top: 10px; margin-left: 25px;">
                  <v-icon>mdi-cloud-upload</v-icon>
                </v-btn>
              </template>
              <span>上传</span>
            </v-tooltip>
          </v-card>
        </v-sheet>
      </v-bottom-sheet>
