</template>


<script>
  import pdf from 'vue-pdf'
  export default {
    name: 'resource_t',
    components: {
      pdf,
      'remote-js': {
        render(createElement) {
          return createElement('script', {
            attrs: {
              type: 'text/javascript',
              src: this.src,
              onload: this.onload
            }
          });
        },
        props: {
          src: {
            type: String,
            required: true
          },
        },
      },
    },
    data() {
      return {
        flag12: false,
        items: ["试卷", "资料"],
        choose_item: 0,
        name: '',
        date: '',
        star: false,
        papers: [],
        pk: 0,
        len: '',
        sheet1: false,
        sheet2: false,
        classes: [],
        selected: [],
        rules: [
          value => !value || value.size < 5000000 || '文件应该小于 5 MB！',
        ],
        star2: '',
        files: [],
        file: '',
        len2: '',
        name2: '',
        date2: '',
        pk2: 0,
        selected2: [],
        dialog2: false,
        paper_choosed: '',
        class_choosed: '',
        min: 0,
        max: 180,
        time: 120,
        loading: false,
        loading2: false,
        loading3: false,
        path: '',
        file_choosed: '',
        
      }
    },
    created() {
      this.$global2.load_config()
      this.$global2.load_css()
      this.get_files()
      this.find_paper()
      this.get_classes()
    },
    // mounted() {
    //   this.path = '../../../../../static/pdf/web/viewer.html?file=' + encodeURIComponent('https://lidn02.o%BA.pdf')   // 找到之前引进来pdf文件中的的viewe.html并+pdf地址
    // },
    computed: {

    },
    methods: {
      get_files: function () {
        var that = this;
        this.$axios({
          method: 'get',
          url: '/api/get_files/',
          params: {
            tea_id: that.$global.userMsg.id,
          },
        })
        .then(function (response) {
          if (response.data.error_num == 0) {
            console.log("资料", response.data)
            that.files = response.data.files.reverse()
            that.len2 = response.data.files.length
          } else {
            alert("获取资料失败！")
          }
        })
      },
      find_paper: function () {
        var that = this;
        this.$axios({
          method: 'get',
          url: '/api/find_paper/',
          params: {
            tec_id: that.$global.userMsg.id,
          }
        })
        .then(function (response) {
          if (response.data.error_num == 0) {
            console.log(response.data)
            that.papers = response.data.papers.reverse()
            that.len = response.data.papers.length
            that.name = response.data.papers[0].name
            that.date = response.data.papers[0].time.slice(0, 10)
            setTimeout(() => {
              that.flag12 = true
              that.$nextTick(function () {
                that.$global2.MathQueue("this_s");
              })
            }, 500)
          } else if (response.data.error_num == 1) {
            alert("获取试卷失败！")
          }
        })
      },
      get_classes: function () {
        var that = this;
        this.$axios({
            method: 'get',
            url: '/api/get_classes/',
            params: {
              tec_id: that.$global.userMsg.id,
            }
          })
          .then(function (response) {
            if (response.data.error_num == 0) {
              console.log(response.data)
              that.classes = response.data.classes
            } else if (response.data.error_num == 1) {
              alert("获取班级失败！")
            }
          })
      },
      choose: function (index) {
        this.pk = index;
        this.name = this.papers[index].name;
        this.date = this.papers[index].time.slice(0, 10);
        this.star = this.papers[index].star;
      },
      choose2: function (index) {
        this.pk2 = index;
        this.name = this.files[index].name;
        this.date = this.files[index].edit_date;
        this.path = this.files[index].path;
        this.star2 = this.files[index].star2;
        // this.dialog2 = true
      },
      sta: function () {
        var that = this;
        if (that.notes[that.pk].star) {
          that.star = false
        } else {
          that.star = true
        }
        this.$axios({
            method: 'get',
            url: '/api/save_note/',
            params: {
              stu_id: that.$global.userMsg.id,
              pk: that.len - that.pk - 1,
              title: that.title,
              content: that.content,
              star: that.star,
            },
          })
          .then(function (response) {
            if (response.data.error_num == 0) {
              console.log(response.data)
              that.papers[that.pk].star = that.star
              if (that.star) {
                alert("收藏成功！")
              } else {
                alert("取消收藏成功！")
              }
            } else {
              alert("收藏 / 取消收藏失败！")
            }
          })
      },
      del: function () {
        var that = this;
        this.$axios({
            method: 'get',
            url: '/api/erase_paper/',
            params: {
              id: that.$global.userMsg.id,
              pk: that.len - that.pk - 1,
            },
          })
          .then(function (response) {
            if (response.data.error_num == 0) {
              console.log(response.data)
              that.get_notes()
              that.reload()
              alert("删除成功！")
            } else {
              alert("删除失败！")
            }
          })
      },
      push_paper: function (i) {
        var that = this;
        this.loading = true;
        this.$axios({
          method: 'get',
          url: '/api/send_paper/',
          params: {
            id: that.$global.userMsg.id,
            paper_index: that.len - that.pk - 1,
            class_index: that.class_choosed,
            duration: that.time
          }
        })
        .then(function (response) {
          if (response.data.error_num == 0) {
            console.log(response.data)
            alert("推送成功！")
            that.loading = false;
            that.sheet1 = !that.sheet1
          } else if (response.data.error_num == 1) {
            alert("推送失败！")
          }
        })
      },
      push_file: function (i) {
        var that = this;
        this.loading3 = true;
        this.$axios({
          method: 'get',
          url: '/api/send_file/',
          params: {
            id: that.$global.userMsg.id,
            file_index: that.len2 - that.pk2 - 1,
            class_index: that.class_choosed,
          }
        })
        .then(function (response) {
          if (response.data.error_num == 0) {
            console.log(response.data)
            alert("推送成功！")
            that.loading3 = false;
            that.sheet3 = !that.sheet3
          } else if (response.data.error_num == 1) {
            alert("推送失败！")
          }
        })
      },
      upload: function () {
        var that = this;
        that.loading2 = true;
        var data = new FormData();
        data.append('id', that.$global.userMsg.id)
        var file = document.getElementById('file').files[0];
        data.append('file', file)
        if (file.size < 5000000) {
          that.$axios({
            method: 'post',
            url: '/api/save_file/',
            data: data,
          })
          .then(function (response) {
            if (response.data.error_num == 0) {
              console.log(response.data)
              that.get_files()
              alert("上传成功！")
              that.loading2 = false;
              that.sheet2 = !that.sheet2
            } else {
              alert("上传失败！")
            }
          })
          setTimeout(() => {
            that.loading2 = false;
          }, 3000)
        }
      },
    },
    watch: {
      pk: {
        handler(newVal, oldVal) {
          var that = this;
          console.log("pk = ", oldVal, newVal)
          that.$nextTick(function () {
            that.$global2.MathQueue("this_s");
          })
        },
        deep: true,
        immediate: true
      },
      choose_item: {
        handler(newVal, oldVal) {
          var that = this;
          console.log("choose_item = ", oldVal, newVal)
          that.$nextTick(function () {
            that.pk = 0
            that.$global2.MathQueue("this_s");
          })
        },
        deep: true,
        immediate: true
      },
    }
  }
</script>